<template>
   <div>
        <select v-model="selected">
          <option disabled value="">구를 선택하시오</option>
          <option>강서구</option>
          <option>양천구</option>
          <option>구로구</option>
          <option>영등포구</option>
          <option>금천구</option>
          <option>동작구</option>
          <option>관악구</option>
          <option>서초구</option>
          <option>강남구</option>
          <option>송파구</option>
          <option>강동구</option>
          <option>마포구</option>
          <option>은평구</option>
          <option>서대문구</option>
          <option>종로구</option>
          <option>중구</option>
          <option>성북구</option>
          <option>강북구</option>
          <option>도봉구</option>
          <option>노원구</option>
          <option>중량구</option>
          <option>동대문구</option>
          <option>성동구</option>
          <option>광진구</option>
        </select>
        <span>선택함: {{selected}}</span>
        <table class="table table-bordered table-condensed">
          <colgroup>
            <col :style="{width: '40%'}"/>
            <col :style="{width: '20%'}"/>
            <col :style="{width: '20%'}"/>
            <col :style="{width: '10%'}"/>
          </colgroup>
          <tr>
            <th>주소</th>
            <th>이름</th>
            <th>업데이트 날짜</th>
            <th>마스크재고</th>
          </tr>
          <template v-if="selected==''">
          <tr v-for="(item, index) in items" :key="index + '_items'">
            <td>{{item.addr}}</td>
            <td><router-link :to="'read?lon=' + item.lng+'&lat='+item.lat">{{item.name}}</router-link></td>
            <td>{{item.stock_at}}</td>
            <template v-if="item.remain_stat == 'plenty'">
            <td>100개이상</td>
            </template>
             <template v-if="item.remain_stat == 'some'">
            <td>30~100개</td>
            </template>
             <template v-if="item.remain_stat ==  'few'">
            <td>2~30개</td>
            </template>
             <template v-if="item.remain_stat == 'empty'">
            <td>1개 이하</td>
            </template>
            <template v-if="item.remain_stat == 'break'">
            <td>판매중지</td>
            </template>
          </tr>
          </template>
          <template v-else>
          <tr v-for="(item,index) in even2(items,selected)" :key="index + '_items'">
            <td>{{item.addr}}</td>
            <td><router-link :to="'read?lon=' + item.lng+'&lat='+item.lat">{{item.name}}</router-link></td>
            <td>{{item.remain_stat}}</td>
          </tr>
          </template>
        </table>
    </div>
</template>

<script>
// var convert = require('xml-js')
export default {
  name: 'list',
  data: function () {
    return {
      items: [],
      selected: ''
    };
  },
  created() {
              //http://apis.data.go.kr/B552657/HsptlAsembySearchService/getHsptlMdcncListInfoInqire?Q0=서울특별시&pageNo=1&numOfRows=300&ServiceKey=C620W5dHM13lKlx2CLrw2AP2n6vXCVt6A0KrnAgD0NXjZxlxXI7QfuM%2B%2B3pjBApMP0zE8pi6VhTBTJHECPdG7A%3D%3D
    this.$http.get('https://8oi9s0nnth.apigw.ntruss.com/corona19-masks/v1/storesByGeo/json')
    .then((response) => {
      var res = JSON.parse(response.request.response)
      this.items = res.stores
      console.log(res.stores)
    });
  },
  methods: {
  even2: function (items,selected) {
    return items.filter(function (item) {
      var result = item.addr.split(" ");
      return result[1] == selected
    })
  },
}
}
</script>

<style lang="scss" scoped>

</style>