<template>
<div id="map" style="width:500px;height:400px;"></div>
</template>

<script>
    import moment   from 'moment'
    export default 
    {
        name: 'read',
        data: function () {
            return {
            items: [],
            lat: 0,
            lon: 0
            }
        },
        created() {
            const params = new URL(document.location).searchParams;
            this.lat = params.get('lat');
            this.lon = params.get('lon');
        },
        mounted() { 
          window.kakao && window.kakao.maps ? this.initMap() : this.addScript(); 
        },
        methods: {
          initMap() 
          { 
            var container = document.getElementById('map'); 
            var options = { center: new kakao.maps.LatLng(this.lat,this.lon), 
            level: 3 
            }; 
            var map = new kakao.maps.Map(container, options); //마커추가하려면 객체를 아래와 같이 하나 만든다. 
            var marker = new kakao.maps.Marker({ position: map.getCenter() }); 
            marker.setMap(map); 
          }, 
          addScript() 
          { 
              const script = document.createElement('script'); /* global kakao */ 
              script.onload = () => kakao.maps.load(this.initMap); 
              script.src = '//dapi.kakao.com/v2/maps/sdk.js?appkey=335dc7134d16940170aa9927b4bd53a6&autoload=false'; 
              document.head.appendChild(script); 
          },
          getFormatDate(regtime) {
            return moment(new Date(regtime)).format('YYYY.MM.DD HH:mm:ss');
          },
        } 
    }
</script>

